body {
  color: #606c76;
  font-family: 'Roboto', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
}


/* Generic styling */

button:focus, input:focus {
    outline: none;
}

.btn {
    background-color: #33C3F0;
    border-radius: 2px;
    color: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border: none;
    cursor: pointer;
    opacity: 1;
}

.btn:active {
    opacity: 0.8;
}

.add-cart-item-enter {
  opacity: 0.01;
}

.add-cart-item-enter.add-cart-item-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

/* Sections */

html, body {
    width: 100%;
    height: 100%;
    margin: 0;
}

#root {
    margin: 0 2rem;
    padding: 0 2rem;
    background-color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.header {
    -ms-flex: 0 1;
        flex: 0 1;
    padding: 1rem 0;
}

.main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex: 1 1;
        flex: 1 1;
    padding-bottom: 2rem;
}

.products {
    -ms-flex: 3 1;
        flex: 3 1;
}

.cart {
    -ms-flex: 2 1;
        flex: 2 1;
}

/* Header */
.searchbar {
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
    padding: 1rem 0;
    border: 3px solid #E9E9E9;
    border-left: 0;
    border-right: 0;
    margin-bottom: 1rem;
    display: -ms-flexbox;
    display: flex;
}

.searchbar > input {
    padding: 5px;
    font-size: 1.3rem;
    border: 1px solid #E9E9E9;
    border-right: none;
}

.searchbar > input[type=submit] {
    background-color: black;
    padding: 1rem;
    border: none;
    border-radius: 2px;
    font-size: 0.8rem;
    color: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

/* Products */

.product-list {
    margin-right: 1rem;
}

.search-results {
    padding-bottom: 1rem;
}

.search-results .search-term {
    font-style: italic;
}

.product {
    padding: 1rem;
    border: 1px solid #E9E9E9;
    border-radius: 2px;
    overflow: auto;
    margin: 1rem 1rem 1rem 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
}

.product:first-child {
    margin-top: 0;
}

.product > div:first-child {

}

.product > div:last-child {
    margin-left: 1rem;
}

.product-image {
    max-height: 150px;
    width: 250px;
    overflow: hidden;
    border: 1px solid #E9E9E9;
    border-radius: 2px;
}

.product-image > img {
    width: 100%;
    display: block;
}

.product-title {
    margin-top: 0;
}

.product .add-to-cart {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
}

#product-list-bottom {
    text-align: center;
    color: #AAAAAA;
    font-size: 0.85rem;
}

/* Cart */

.cart {
    margin-left: 1rem;
    padding-left: 1rem;
}

.cart .empty-cart {
    padding-top: 1rem;
}

.cart > h2 {
    margin-top: 0;
}

.cart ul {
    width: 100%;
    font-size: 0.9rem;
    border-top: 2px solid black;
    list-style: none;
    margin: 0;
    padding: 0;
}

.cart ul li {
    vertical-align: top;
    padding: 1rem 1rem 1rem 0;
}

.cart ul .cart-item {
    border-bottom: 1px solid #E9E9E9;
}

.cart ul .cart-item .item-title {

}

.cart ul .cart-item .item-price {
    font-weight: bold;
    padding-top: 0.5rem;
}

.cart ul .cart-item .item-qty {
    margin-right: 1rem;
}

.cart ul .cart-item:last-child {
    border-bottom: none;
}

.cart ul .cart-item button {
    margin-right: 3px;
}



